input[type="checkbox"] {
  appearance: none;

  position: relative;

  box-sizing: border-box;
  display: inline-block;
  margin-top: 4px !important;
  width: 40px;
  height: 16px;

  border: 1px solid #aaa;
  border-radius: 2px;
  background: #ebebeb;
  overflow: hidden;
  vertical-align: middle;
  transition: background 0.3s;

  &:after {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    width: 14px;
    height: 14px;
    background: white;
    border: 1px solid #aaa;
    border-radius: 2px;
    transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }

  &:checked {
    background: #a6c7ff;
    border-color: #8daee5;

    &:after {
      left: 23px;
      border-color: #8daee5;
    }
  }

  &:hover:not(:checked):not(:disabled):after,
  &:focus:not(:checked):not(:disabled):after {
    left: 0;
  }

  &:hover:checked:not(:disabled):after,
  &:focus:checked:not(:disabled):after {
    left: 22px;
  }

  &:disabled {
    opacity: 0.5;
  }
}


.widefat.cosr-widefat {
  margin: 1em 0 2em;

  thead tr th {
    padding: 14px 10px;

    font-size: 18px;
    font-weight: 500;
  }

  tbody th {
    padding: 12px 10px;
    width: 100%;

    border-bottom: 1px solid rgba(238, 238, 238, 0.5);

    &:last-child {
      border-bottom: none;
    }

    input {
      margin: 4px 0 2px;
      padding: 4px 5px;

      box-shadow: none;
    }
  }
}

